import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      uid: 0,
      name: '',
      pass: '123456',
      img: 'http://39.108.80.57:81/images/ooopic_128.png'
    },
    iceconfig: {
      'iceServers': [{
        'url': 'stun:39.108.80.57'
      }]
    },
    defimag: 'http://39.108.80.57:81/images/ooopic_128.png',
    userlist: [], // [{name: 'test', img: 'http://39.108.80.57:3000/images/head_goat_256px.png'}],
    cameraSwitch: true,
    audioSwitch: false,
    streams: [],
    room: '-',
    loginState: 0
  },
  getters: {
    getICE: state => state.iceconfig
  },
  mutations: {
    IO_CHANNEL_INIT (state) {
      console.log('----------IO_CHANNEL_INIT-------')
    },
    STATE_USERNAME (state, n) {
      state.user.name = n.n
      state.user.uid = n.uid
    },
    STATE_USERPASS (state, p) {
      state.user.pass = p
    },
    JOIN_ROOM (state, u) {
      state.userlist.push(u)
    },
    REMOVE_ROOM (state, u) {
      for (let i = 0; i < state.userlist.length; i++) {
        let n1 = state.userlist[i].name
        let n2 = u.name
        if (n1 === n2) {
          state.userlist.splice(i, 1)
          break
        }
      }
    },
    JOIN_STREAM (state, u) {
      console.log('aaaaaaaaaa----------add streams-----', u)
      state.streams.push(u)
    },
    REMOVE_STREAM (state, u) {
      for (let i = 0; i < state.streams.length; i++) {
        let n1 = state.streams[i].myid
        let n2 = u.myid
        if (n1 === n2) {
          state.streams.splice(i, 1)
          break
        }
      }
    },
    SET_CAMERA_ONOFF (state, u) {
      state.cameraSwitch = u
    },
    SET_AUDIO_ONOFF (state, u) {
      state.audioSwitch = u
    },
    SET_ROOM (state, u) {
      state.room = u
    },
    SET_LOGINSTATE (state, u) {
      state.loginState = u
    }
  },
  actions: {
    ioInit: ({ state }) => {
      console.log('ioInit')
    },
    search: ({ commit }, value) => commit('SET_FILTER_KEY', value),
    auth: ({ state }) => {
      console.log('-----store----auth--------')
    }
  }
})

