<template>
  <div class="hello">
    <div class='roomtitle'>房间: {{ room }}</div>
    
      <vwrap class='pai' v-for="st in vs" :stobj="st.stm" :key="st.myid"></vwrap>
    
    <video class='vlocal' ref='localcamera' autoplay></video>
  </div>
</template>

<script>
import channel from '../api/iochannel'
import vwrap from './VideoWrap'
export default {
  computed: {
    room: function () { return decodeURIComponent(this.$store.state.room) },
    vs: function () { return this.$store.state.streams }
  },
  components: { vwrap },
  mounted: function () {
    console.log('mounted----mainView-------')
    channel.init(this.$refs.localcamera)
    // channel.init(this.$refs.localcamera, this.$refs.rvideo1, this.$refs.rvideo2, this.$refs.rvideo3)
  },
  methods: {
    update: function () {
      console.log('---------update--------')
      for (let v in this.vs) {
        let vv = this.vs[v]
        console.log('---------update-----vvvv---', vv.myid, this.$refs)
        if (this.$refs[vv.myid]) {
          console.log('-------------set stream----')
          this.$refs[vv.myid][0].srcObject = vv.stm
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.roomtitle {
  width: 100%;
  height: 20px;
  color: #f4f4f4;
  background-color: #2e3238;
}
.pai {
  background-color: #a4a4a4;
}
.vlocal {
  width: 280px;
  height: 280px;
  margin: 5px;
  border: solid 1px green; 
}
</style>



