<script>
import store from '../store'
export default {
  computed: {
    user: () => store.state.user
  },
  props: ['stobj'],
  mounted () {
    console.log('---------video wrap----mounted----')
    this.$refs.rvideo1.srcObject = this.stobj
  },
  methods: {
    onKeyup (e) {
      console.log('--------------onKeyup---------')
      console.log('---store:', store.state.user)
    }
  }
}
</script>

<template>

    <video class='vclass' ref='rvideo1' autoplay ></video>

</template>

<style scoped>
.vclass{
  width: 280px;
  height: 280px;
  margin: 5px;
  border: solid 1px red; 
}
</style>



