<script>
import st from '../store'
export default {
  computed: {
    users: () => st.state.userlist
  },
  mounted () {
    console.log('-----monted list-----')
  }
}
</script>

<template>
<div class="list2">
        <div v-for="i in users" class="active2" >
            <img class="avatar"  width="30" height="30" :alt="i.name" :src="i.img">
            <p class="uname">{{i.name}}</p>
        </div>
</div>
</template>

<style scoped>
.list, li {
    padding: 3px 3px;
    border-bottom: 1px solid #292C33;
    cursor: pointer;
    transition: background-color .1s;
}
li {
    padding: 3px 3px;
    border-bottom: 1px solid #292C33;
    cursor: pointer;
    transition: background-color .1s;
}
.active {
    background-color: rgba(255, 255, 255, 0.1);
}
.active2 {
    border-bottom: solid 1px #292C33;
    height: 40px;
}
.avatar, .uname {
    vertical-align: middle;
}
.avatar {
    border-radius: 2px;
}
.uname {
    display: inline-block;
}

</style>



