<script>
import st from '../store'
import channel from '../api/iochannel'
export default {
  data () {
    return {
      debug: false,
      setroom: {},
      seleindex: [],
      // rooms: ['烟雨楼', '怡红院', '龙门客栈', '小楼一夜听风雨']
      rooms: []
    }
  },
  computed: {
    camonoff: {
      get: () => st.state.cameraSwitch,
      set: (s) => st.commit('SET_CAMERA_ONOFF', s)
    },
    audonoff: {
      get: () => st.state.audioSwitch,
      set: (s) => st.commit('SET_AUDIO_ONOFF', s)
    },
    username: {
      get () {
        return st.state.user.name
      },
      set (val) {
        st.commit('STATE_USERNAME', val)
      }
    },
    userpass: {
      get () {
        return st.state.user.pass
      },
      set (val) {
        st.commit('STATE_USERPASS', val)
      }
    },
    room: {
      get () {
        return st.state.room
      },
      set (val) {
        st.commit('SET_ROOM', val)
      }
    },
    tt: {
      get () {
        return this.rooms
      }
    }
  },
  mounted () {
    console.log('-----monted ctrl-----:')
    let that = this
    channel.httppost('/api/guestlogin', 'lat=12.123&lng=113.4434', function (er, da) {
      let uinfo = JSON.parse(da)
      if (uinfo.code !== 0) {
        console.log('------login error:', da)
        return
      }
      st.commit('STATE_USERNAME', uinfo)
      channel.httpget('/api/getrooms?lat=12.123&lng=113.4434', function (er2, da2) {
        let tt = JSON.parse(da2)
        if (tt && tt.rooms && tt.rooms.length > 0) {
          that.rooms = tt.rooms
        }
        channel.authIO()
      })
    })
  },
  methods: {
    auth: function () {
      console.log('------2----auth--------')
      channel.auth()
    },
    start: function () {
      console.log('start local stream')
    },
    stop: function () {
      channel.stop()
    },
    enterroom: function () {
      // let r = encodeURIComponent(this.setroom)
      channel.reqJoin(this.setroom)
    },
    test: function () {
      channel.httpget('/api/getOnlines/0', (err, data) => console.log('----http test:' + data + ' err:' + err))
      // channel.stop()
      // this.$store.commit('JOIN_ROOM', {name: 'add1', img: 'http://39.108.80.57:3000/images/head_goat_256px.png'})
      st.commit('REMOVE_ROOM', {name: 'm2'})
      st.commit('STATE_USERNAME', 'heiehie')
    },
    selectchange: function (ev) {
      console.log('----selectchange---:', ev)
      this.setroom = ev // JSON.parse(ev)
    }
  }
}
</script>

<template>
<div>
	<div class='ctlitem'>
		<div class='la'>camera:</div>
		<el-switch
		    v-model="camonoff"
		    on-color="#13ce66"
		    off-color="#ff4949">
		</el-switch>
	</div>
	<div class='ctlitem'>
		<div class='la'>audio:</div>
		<el-switch
		    v-model="audonoff"
		    on-color="#13ce66"
		    off-color="#ff4949">
		</el-switch>
	</div>
	<div class='ctlitem' id='usera'>
    <el-input placeholder="null" v-model="setroom.t" size="small">
        <template slot="prepend">Room</template>
      </el-input>
    <el-select v-model="seleindex" clearable placeholder="请选择" @change='selectchange'>
      <el-option
        v-for="item in rooms" :key="item.t"
        :label="item.t"
        :value="item">
      </el-option>
    </el-select>
      <el-button :plain="true" size="small" type="primary" @click="enterroom">Join</el-button><br/><br/>

    <el-input placeholder="null" v-model="username" size="small">
        <template slot="prepend">username</template>
      </el-input>
    <el-input placeholder="null" v-model="userpass" size="small">
        <template slot="prepend">password</template>
      </el-input>
    <el-button :plain="true" size="small" type="primary" @click="auth" :disabled="!debug">Login</el-button>
      <button v-if='debug' @click='start'>start</button>
      <button v-if='debug' @click='test'>test</button>
	</div>
</div>
</template>

<style scoped>
.ctlitem {
	margin: 10px 10px;
}
.la {
	float: left;
	width: 80px;
	text-align: left;
}
#usera {
	border: solid 0px red;
}
</style>



