import io from 'socket.io-client'
import st from '../store'

var client = io('https://39.108.80.57')
// var client = io()
var ROOM = 'manyroot'
var ruserlist = []

function getRuser (n) {
  n = n + ''
  for (let i = 0; i < ruserlist.length; i++) {
    if (ruserlist[i].name === n) return ruserlist[i]
  }
}

function removeRuser (n) {
  for (let i = 0; i < ruserlist.length; i++) {
    if (ruserlist[i].name === n) {
      ruserlist.splice(i, 1)
      return
    }
  }
}
/*
function sendMsg (usr, message) {
  console.log('Client sending message: ', message)
  // client.emit('msg', message)
  var msg = {to: usr, bdy: message}
  client.emit('touser', msg)
}
*/
function remoteUsr (usr) {
  usr = usr + ''
  var htmlv = remoteVs.shift()
  function iceCandidate (event) {
    console.log('------ice cadidate---usr:', usr)
    if (event.candidate) {
      /* sendMsg(usr, {
        type: 'candidate',
        label: event.candidate.sdpMLineIndex,
        id: event.candidate.sdpMid,
        candidate: event.candidate.candidate
      }) */
      sendWebrtcMsg(usr, {
        type: 'candidate',
        label: event.candidate.sdpMLineIndex,
        id: event.candidate.sdpMid,
        candidate: event.candidate.candidate
      })
    } else {
      console.log('End of candidates.--------usr:', usr)
    }
  }
  function remoteStreamAdded (event) {
    console.log('Remote stream added.------usr:', usr)
    // if (htmlv) htmlv.srcObject = event.stream
    st.commit('JOIN_STREAM', {myid: usr, stm: event.stream})
  }
  function remoteStreamRemoved (event) {
    console.log('-----Remote stream removed. usr,event: ', usr, event)
    htmlv.srcObject = null
    remoteVs.push(htmlv)
  }
  function leave () {
    // htmlv.srcObject = null
    // remoteVs.push(htmlv)
    st.commit('REMOVE_STREAM', {myid: usr})
  }

  try {
    let p = new RTCPeerConnection(pcConfig)
    console.log('----------peer connection ok:', p)
    p = null
  } catch (e) {
    console.log('Failed to create PeerConnection, exception: ' + e.message)
    alert('Cannot create RTCPeerConnection object.')
    return
  }

  var rpc = new RTCPeerConnection(pcConfig)
  rpc.onicecandidate = iceCandidate
  rpc.onaddstream = remoteStreamAdded
  rpc.onremovestream = remoteStreamRemoved
  console.log('Created RTCPeerConnnection------usr:', usr)
  if (localStream) {
    rpc.addStream(localStream)
  }
  function setLocalAndSend (sessionDescription) {
    console.log('------------createOffer------:', sessionDescription)
    rpc.setLocalDescription(sessionDescription)
    // sendMsg(usr, sessionDescription)
    sendWebrtcMsg(usr, sessionDescription)
  }
  return {name: usr, rpc: rpc, setLocal: setLocalAndSend, leave}
}

function onGetOffer (from, bdy) {
  var rusr = remoteUsr(from)
  ruserlist.push(rusr)
  rusr.rpc.setRemoteDescription(new RTCSessionDescription(bdy))
  rusr.rpc.createAnswer().then(
    rusr.setLocal,
    onCreateSessionDescriptionError
  )
  console.log('-------------------------join room usr:', from)
  st.commit('JOIN_ROOM', {name: from, img: 'http://39.108.80.57:81/images/ooopic_128.png'})
}

client.on('msg', function (message) {
  console.log('----msg:', message)
  // st.dispatch('auth')
  var from = message.from
  var to = message.to
  var msgtype = message.type
  var msg = message.msg
  var bdy = message.bdy
  console.log('--------from:', from)
  if (msgtype) {
    console.log('---------on msg:', to, msgtype, msg)
    switch (msgtype) {
      case 4:
        if (msg.type === 'offer') {
          console.log('------------------------offer---------')
          onGetOffer(from, msg)
        } else if (msg.type === 'answer') {
          let u = getRuser(from)
          console.log('------------------------answer---------u:', u, from)
          if (u) {
            console.log('------------------------answer---------u1')
            u.rpc.setRemoteDescription(new RTCSessionDescription(msg))
            console.log('------------------------answer---------u2')
          }
        } else if (msg.type === 'candidate') {
          let candidate = new RTCIceCandidate({
            sdpMLineIndex: msg.label,
            candidate: msg.candidate
          })
          let ur = getRuser(from)
          console.log('--------add ice candidate----,', ur, from)
          if (ur) ur.rpc.addIceCandidate(candidate)
        }
        break
      case 5:
        if (msg.t === 3) {
          console.log('-----joinret', msg)
          enterRoom(ROOM.id, (er) => {
            sendRoomReady(ROOM.id)
          })
          // client.emit('notifyready', {room: ROOM.id})
          // setTimeout(() => sendRoomReady(ROOM.id), 1000)
          // sendRoomReady(ROOM.id)
          console.log('--------room t', ROOM)
          st.commit('SET_ROOM', ROOM.t)
        } else if (msg.t === 1) {
          console.log('----------------msg userjoined:', msg.uid)
          let uu = remoteUsr(msg.uid)
          ruserlist.push(uu)
          uu.rpc.createOffer(uu.setLocal, (er) => console.log('-------createOffer err:', er))
          st.commit('JOIN_ROOM', {name: msg.uid, img: 'http://39.108.80.57:81/images/ooopic_128.png'})
        } else if (msg.t === 2) {
          console.log('----------------msg userjoined:', msg.uid)
          let u2 = remoteUsr(msg.uid)
          u2.rpc.close()
          u2.rpc = null
          u2.leave()
          removeRuser(msg.uid)
          u2 = null
          console.log('------htmlvs---len:', remoteVs.length)
          st.commit('REMOVE_ROOM', {name: msg.uid})
        }
        break
    }
    return
  }
  if (bdy.type === 'offer') {
    console.log('------------------------offer---------')
    onGetOffer(from, bdy)
  } else if (bdy.type === 'answer') {
    var u = getRuser(from)
    console.log('------------------------answer---------u:', u)
    if (u) {
      console.log('------------------------answer---------u1')
      u.rpc.setRemoteDescription(new RTCSessionDescription(bdy))
      console.log('------------------------answer---------u2')
    }
  } else if (bdy.type === 'candidate') {
    var candidate = new RTCIceCandidate({
      sdpMLineIndex: bdy.label,
      candidate: bdy.candidate
    })
    var ur = getRuser(from)
    console.log('--------add ice candidate----,', ur)
    if (ur) ur.rpc.addIceCandidate(candidate)
  } else if (bdy === 'usrjoined') {
    var uu = remoteUsr(from)
    ruserlist.push(uu)
    console.log('--------usrjoined--------')
    uu.rpc.createOffer(uu.setLocal, (er) => console.log('-------createOffer err:', er))
    st.commit('JOIN_ROOM', {name: from, img: 'http://39.108.80.57:81/images/ooopic_128.png'})
  } else if (bdy === 'usrleave') {
    let u2 = remoteUsr(from)
    u2.rpc.close()
    u2.rpc = null
    u2.leave()
    removeRuser(from)
    u2 = null
    console.log('------htmlvs---len:', remoteVs.length)
    st.commit('REMOVE_ROOM', {name: from})
  }
})

client.on('joinret', function (msg) {
  console.log('-----joinret', msg)
  if (msg.code < 0) return
  enterRoom(ROOM.id)
  setTimeout(client.emit('notifyready', {room: ROOM.id}), 1000)
  // client.emit('notifyready', {room: ROOM.id})
  console.log('--------room t', ROOM)
  st.commit('SET_ROOM', ROOM.t)
})

client.on('connect', () => {
  console.log('-------connected----,user:' + st.state.user.name)
  setConnectState(1)
})

client.on('disconnect', () => {
  console.log('-------disconnected----')
})

function sendRoomJoin (roomid) {
  let joinmsg = {'from': st.state.user.uid, 'to': roomid, 'type': 5, 'msg': {t: 1}}
  client.emit('msg', joinmsg)
}
function sendRoomReady (roomid) {
  let joinmsg = {'from': st.state.user.uid, 'to': roomid, 'type': 5, 'msg': {t: 5}}
  client.emit('msg', joinmsg)
}
function sendWebrtcMsg (to, body) {
  let joinmsg = {'from': st.state.user.uid, 'to': to, 'type': 4, 'msg': body}
  console.log('---send webrtc msg:', joinmsg)
  client.emit('msg', joinmsg)
}
function authIO () {
  let uid = st.state.user.uid
  let usr = st.state.user.name
  let pass = st.state.user.pass
  client.emit('auth', {uid: uid, name: usr, passwd: pass}, (dd) => {
    console.log('---auth:', dd)
    if (dd === 'ok') {
      setConnectState(2)
    }
  })
}
function httpget (url, callback) {
  url = 'https://39.108.80.57' + url
  fetch(url, {credentials: 'include', mode: 'cors'}).then(function (resp) {
    console.log('fetch:' + resp.statusText)
    return resp.text()
  }).then(function (jso) {
    callback(null, jso)
  })
}
function httppost (url, str, callback) {
  url = 'https://39.108.80.57' + url
  fetch(url, {
    credentials: 'include',
    mode: 'cors',
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    body: str})
  .then(function (resp) {
    console.log('fetch:' + resp.statusText)
    return resp.text()
  }).then(function (jso) {
    callback(null, jso)
  })
}

var localStream
var pcConfig = {
  'iceServers': [{
    'urls': 'stun:39.108.80.57'
  },
  {
    'urls': 'turn:39.108.80.57',
    'username': 'my',
    'credential': '123456'
  }]
}
// pcConfig.iceServers = st.state.iceconfig

var remoteVs = []
var localVideo = {}

window.onbeforeunload = function () {
  console.log('----------on before unload----------')
}

function onCreateSessionDescriptionError (error) {
  console.log('Failed to create session description: ' + error.toString())
}

function setConnectState (s) {
  st.commit('SET_LOGINSTATE', s)
}

function init (localV, v1, v2, v3) {
  console.log('-----io channel init----')
  localVideo = localV
  remoteVs.push(v1)
  remoteVs.push(v2)
  remoteVs.push(v3)
}

function reqJoin (usr) {
  ROOM = usr
  // client.emit('join', {room: usr.id})
  sendRoomJoin(usr.id)
}

function enterRoom (room, callback) {
  console.log('Requesting local stream')
  // ROOM = room
  // startButton.disabled = true
  let vid = st.state.cameraSwitch
  let aud = st.state.audioSwitch
  navigator.mediaDevices.getUserMedia({
    audio: aud,
    video: vid
  })
  .then(function (stream) {
    localVideo.srcObject = stream
    localStream = stream
    console.log('----------------localStream created-----')
    callback(null)
  })
  .catch(function (e) {
    alert('getUserMedia() error: ' + e)
    callback(e)
  })
}

export default {
  auth: () => {
    let usr = st.state.user.name
    let pass = st.state.user.pass
    client.emit('auth', {name: usr, passwd: pass}, (dd) => console.log(dd))
  },
  httpget: httpget,
  httppost,
  init: init,
  stop: stop,
  reqJoin,
  authIO
}



