// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'webrtc-adapter'
import store from './store'
import { Switch, Table, TableColumn, Input, Button, Select, Option } from 'element-ui'
// import ElementUI from 'element-ui'
// import 'element-ui/lib/theme-default/index.css'
import 'element-ui/lib/theme-default/switch.css'
import 'element-ui/lib/theme-default/table.css'
import 'element-ui/lib/theme-default/table-column.css'
import 'element-ui/lib/theme-default/input.css'
import 'element-ui/lib/theme-default/button.css'
import 'element-ui/lib/theme-default/select.css'

Vue.use(Switch)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Input)
Vue.use(Button)
Vue.use(Select)
Vue.use(Option)
// Vue.use(ElementUI)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



