<script>
import store from '../store'
export default {
  computed: {
    user: () => store.state.user,
    loginstate: () => {
      let s = store.state.loginState
      if (s === 0) return '未连接'
      else if (s === 1) return '已连接'
      else if (s === 2) {
        if (store.state.user.name.indexOf('Guest') === 0) return '游客已登录'
        else return '已登录'
      }
    }
  },
  methods: {
    onKeyup (e) {
      console.log('--------------onKeyup---------')
      console.log('---store:', store.state.user)
    }
  }
}
</script>

<template>
<div class="card">
    <header>
        <img class="avatar" width="40" height="40" :alt="user.name" :src="user.img">
        <p class="name">{{user.name}}</p>
        <div>状态:  {{ loginstate }}</div>
    </header>
</div>
</template>

<style scoped>
.card {
    padding: 12px;
    border-bottom: solid 1px #24272C;

    footer {
        margin-top: 10px;
    }

    .avatar, .name {
        vertical-align: middle;
    }
    .avatar {
        border-radius: 2px;
    }
    .name {
        display: inline-block;
        margin: 0 0 0 15px;
        font-size: 16px;
    }
    .search {
        padding: 0 10px;
        width: 100%;
        font-size: 12px;
        color: #fff;
        height: 30px;
        line-height: 30px;
        border: solid 1px #3a3a3a;
        border-radius: 4px;
        outline: none;
        background-color: #26292E;
    }
}
</style>



